.footer {
  grid-area: 6 / 1 / 7 / -1;
  display: grid;

  height: 200px;
  width: 100%;

  background-image: url("../images/footer.jpg");
  background-size: cover;
  background-position: center;

  @include onTablet {
    height: 480px;
  }

  @include onPC {
    height: 555px;
    display: none;
  }
}
